import React, {Component} from 'react'
import './Footer.css'
//import Instagram from './Instagram/Instagram';
export default class Footer extends Component {
    render() {
        return (
            <footer className='container-fluid mt-1'>
            
                <div className='row'>
                    <div className='col-sm-12 ml-2'>
                        <p className='mt-2 '>Адрес:  
                            <span className='font_8'>  пр-д Байдукова 3, Пенза</span>
                        </p>
                        <em className='mt-2  '>Номер для записи:
                            <a href='tel:+78412306806' className='color-grey  font_2'>+7(8412)<strong style={{ fontSize: '38px', color: 'black' }} >306-806</strong>
                            </a>
                        </em>
                        <p className='mt-4'>© 2015 Центр по уходу за автомобилем «Чистая тонна»</p>
                    </div>
                    <div className='col'>
                        {/*
                        <Instagram/>
                    */}
                    </div>
                </div>
            </footer>
        )
    }
}



