import React, {Component} from 'react'
import {NavLink} from 'react-router-dom'

export default class ListItemLink extends Component {
    render() {
        const {
            to,
            ...rest
        } = this.props
        return (
            <li className='nav-item mx-auto'>
                <NavLink
                    className=' font_7 nav-link'
                    to={to}
                    activeClassName='active'
                    exact strict
                    {...rest}/>
            </li>

        )
    }

}


