import React, {Component} from 'react'
import {InstagramView} from '../Instagram/InstagramView';
import ServiceHeader from './ServiceHeader/Header';
import CustomBody from './ServiceBody/CustomBody';
import ServiceBody from './ServiceBody/ServiceBody';

export default class ServiceView extends Component {
    render() {
        const {items, slogan, about, hashTag, isCustom} = this.props.service

        return (
            <div className='container mt-4'>
                <ServiceHeader about={about} slogan={slogan}/> {isCustom
                    ? <CustomBody items={items}/>
                    : <ServiceBody items={items}/>}

                <InstagramView hashTag={hashTag}/>
            </div>

        )
    }
}



