import React from 'react'
import {NavLink} from 'react-router-dom'
import './NavBar.css'

export const DropdownItemLink = ({
    to,
    ...rest
}) => <NavLink
    to={to}
    exact
    className={'dropdown-item font_7'}
    activeClassName='active'
    {...rest}/>


