import React, {Component} from 'react'
import Slider from "react-slick";
import Nadpis from '../Nadpisi/Nadpisi';
//className="d-block col-4 w-100"
import './Carousel.css'
class CustomSlide extends Component {
    render() {
        const {
            itemLink,
            ...props
        } = this.props;
        return (
            <div {...props} className='p-lg-3'>
                <img src={itemLink} alt="Second slide"/>
            </div>
        );
    }
}

export default class Carousel extends Component {

    render() {
        const {photos} = this.props

        const settings = {
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 2,
            autoplaySpeed: 5000,
            autoplay: true,
            className: "center",
            focusOnSelect: true,
            initialSlide: -1,
            pauseOnHover: true,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        infinite: true,
                        dots: true
                    }
                }, {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        initialSlide: 2
                    }
                }, {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        }

        if (!this.props.photos) 
            return <div/>
        return (
            <Slider style={{
                height: 300
            }} {...settings}>
                <div>

                    <div className='p-lg-3 clearfix'>
                        <div className='p-4  Black-block d-flex align-items-end '>

                            <a href='https://www.instagram.com/cleantonn/' className='m'>

                                <Nadpis text='Галерея наших работ'/>
                                <Nadpis text='в instagram' isBlue/>
                            </a>
                        </div>
                    </div>
                </div>
                {photos.map((val, i) => <CustomSlide key={i} itemLink={val.display_url}/>)}
            </Slider>
        )

    }
}


