export class InstagramView extends Component {
    render() {
        //const {hashTag} = this.props
        return (
            <div className='row mt-3 '>
                
                {/*<InstagramFeed sm='4' hashTag={hashTag} id='hashtagPhotos2'/>*/}
                <InstagramFeed sm='12' hashTag={'cleantonn_блог'} id='hashtagPhotos'/>
            </div>
        )
    }
}
InstagramView.propType = {
    hashTag: PropTypes.string
}

export class InstagramView1 extends Component {
    render() {
        const {hashTag} = this.props
        return (
            <div className='row mt-3 '>
                <div className='col-sm-3  '>
                    <BlackBlock className=''>
                        <a href='https://www.instagram.com/cleantonn/'>
                            <Nadpis text='Галерея наших работ'/>
                            <Nadpis text='в instagram' isBlue/>
                        </a>
                    </BlackBlock>
                </div>
                <InstagramFeed sm='3' hashTag={hashTag} id='hashtagPhotos2'/>
                <InstagramFeed sm='3' hashTag={'cleantonn_блог'} id='hashtagPhotos'/>
            </div>
        )
    }
}


