import React from 'react'
import Mark from './Mark';
const Marks = (props) => (
    <div className='d-flex flex-column p-3'>
        <Mark name='Увеличение блеска:' mark={props.values[0]}/>
        <Mark name='Глубина цвета:' mark={props.values[1]}/>
        <Mark name='Устойчивость к царапинам:' mark={props.values[2]}/>
        <Mark name='Самоочищение:' mark={props.values[3]}/>
        <Mark name='Стойкость:' mark={props.values[4]}/>
    </div>
)
export default Marks


