import React from 'react'
import BlackBlock from '../../hoc/BlackBlock/BlackBlock';
import Nadpis from '../Nadpisi/Nadpisi';
import logo2 from '../../images/logo2.png'

const AdvantagesHeader = () => (
    <div className='row justify-content-lg-center'>
        <div className='col-sm-4'>
            <BlackBlock >
                <Nadpis text='CARS CARE'/>
                <Nadpis
                    isCombo
                    obj={{
                    needSpace: false,
                    blue: '&',
                    white: 'RESTORE',
                    firstBlue: true
                }}/>

            </BlackBlock>
        </div>
        <div className='col-sm text-lg-right'>
            <img src={logo2} className='img-fluid' alt=''/>
        </div>
    </div>
)
export default AdvantagesHeader


