import React from 'react'
import {Link} from 'react-router-dom'
import LogoImage from '../../images/logo.png'

const Logo = () => (
    <Link to='/' className='navbar-brand h-100'>
        <img src={LogoImage} className='img-fluid' width='250' alt="Главная"/>
    </Link>

)

export default Logo


