import React, {Component} from 'react'
import GroupOfStars from './GroupOfStarts';
import PropTypes from 'prop-types'

import './Mark.css'

const Mark = (props) => (
    <div >
        <strong className="font_6">{props.name}</strong>
        <GroupOfStars numerTrue={props.mark} numerAll={5}/>
    </div>
)

export default Mark

Mark.propType = {
    name: PropTypes.string.isRequired,
    mark: PropTypes.number.isRequired
}


